body {
  margin: 0;
  padding: 0;
  background: #F2F2F2;
}
.registration {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.registration__background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.registration__background-a,
.registration__background-b,
.registration__background-c {
  position: absolute;
}
.registration__background-a {
  top: 0;
  right: 0;
}
.registration__background-b {
  right: 15%;
  bottom: 25%;
}
.registration__background-c {
  bottom: 10%;
  left: 15%;
}
.registration__window {
  width: 555px;
  height: 742px;
  box-sizing: border-box;
  padding: 50px 98px 60px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 2px 80px rgba(37, 38, 49, 0.05);
}
